@media screen and (min-width:1400px) {
  .modal-open {
    overflow:auto !important;
  }

  .containerModal{
    position: fixed;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    width: 810px; 
    max-width:100%;
    max-height: 100%;
  }

  .modal-dialog {
    display: flex;
    max-width: 100%;
    flex-direction:row;
    justify-content: center; 
    margin: auto;
  } 

  .modal-content {
    justify-content: center;
    margin:auto;
    border:3px solid white;
  }

  .modalBody {
    display: flex;
    flex-direction:row;
    align-items: center;
    justify-content: center;
    background-color:rgb(221, 221, 221);
    padding: 0px;
  }
  
  .closeModalIcon {
    position: absolute;
    color:white;
    top:-15px;
    right:8px;
    text-align: center;
    border: none;
    height:45px;
    font-size:45px;
    z-index: 1;
    cursor:pointer;
  }
}

