@media screen and (min-width:1400px) {
    .mobileHeader {
        display: none;
    }
    .headerDesktop {
        flex-direction: row;
        flex-wrap: wrap;
        justify-content: center;
        align-items: center;
        margin:auto;
    }

    .logoContainer{
        display:flex;
        flex:auto;
        width:200px;
        height:110px;
        justify-content: center;
        align-items: center;
        text-align: center;
        margin: auto;
        outline: none;
    }

    .imgLogo{
        align-items: center;
        justify-content: center;
        max-width: 70%;
        max-height: 70%;
        margin: auto;
    }

    .navigationBar {
        width:100vw;
        display:flex;
        flex-direction: row;
        flex: auto;
        flex-wrap: wrap;
        align-content: space-around;
        align-items: center;
        justify-content: space-evenly;
        text-align: center;
        font-family: Montserrat;
        outline: none;
    }

    .buton {
        flex: auto;
        background-color:transparent;
        border: none;
        color:rgb(238, 238, 238);
        transition: none;
    }

    .buton:focus {
        outline: none;
    }

    .navigationBar:hover .buton {
        opacity: .55;
    }

    .navigationBar .buton:hover {
        opacity: 5;
        background-color: transparent;
    } 

}

